<template>
  <div class="center" v-loading.body="loading">
    <div class="center_breadcrumb">
      <Crumb />
    </div>
    <div class="center_content" v-if="haveInfo">
      <div class="center_content_name">
        <el-button size="small" style="float: right; margin: 10px 10px 0 0" @click="editRow"><SvgIcon icon-class="iconbianji1"></SvgIcon>&nbsp;&nbsp;编辑</el-button>
        <p v-text="data.versionVal"></p>
        <div style="padding-left: 20px; color: #999; font-size: 14px"><i class="el-icon-date" style="margin-right: 10px"></i>{{data.modifyDate}}</div>
      </div>
      <div class="center_content_info" id="objectiveInfo">
        <!-- <h4 style="padding: 20px; background: #f8faff">本专业的培养目标</h4> -->
        <div id="introduce"  class="introduce" v-html="data.content"></div>
      </div>
      <div class="list">
        <ul>
          <li class="targetItem" v-for="(items, index) in data.goals" :key="index" :title="items.content">
              <div class="index">
                <img v-if="(index + 1) % 4 === 1 || (index + 1) % 4 === 2" src="@/assets/img/bianhao.png" alt="">
                <img v-if="(index + 1) % 4 === 3 || (index + 1) % 4 === 0" src="@/assets/img/bianhao2.png" alt="">
                <span>{{items.code}}</span>
              </div>
              <div class="itemContent">{{items.content}}</div>
          </li>
        </ul>
      </div>
    </div>
    <div class="center_content" v-else>
      <!-- <div class="center_content_title">
        <SvgIcon icon-class="icondatixitongguanli" size="20px"></SvgIcon>
        <span>{{pageTitle}}</span>
      </div> -->
      <div
        style="height: 500px; background-color: #fff; box-shadow: 2px 2px 5px rgba(0, 0, 0, .2); text-align: center; padding-top: 100px; box-sizing: border-box"
      >
        <div>
          <img
            src="@/assets/training.png"
            alt
            style="width: 120px; margin: 0 auto; margin-bottom: 40px"
          />
        </div>
        <el-button icon="el-icon-plus" style="width: 200px" @click="create">创建培养目标</el-button>
      </div>
    </div>
  </div>
</template>

<script>
import trainingProgramApi from "@/api/trainingProgramApi";
export default {
  name: "TrainingObjective",
  data() {
    return {
      pageTitle: "培养目标",
      pageTitleArr: [],
      data: {},
      tableData: [],
      page: {
        keywords: "",
        page: 1,
        rows: 15
      },
      haveInfo: false,
      pageName: "",
      loading: true,
      tmpId: ''
    };
  },
  created() {},
  mounted() {
    this.getInfo();
  },
  methods: {
    getInfo() {
      if (localStorage.getItem("versionId")) {
        this.page.keywords = localStorage.getItem("versionId");
        this.initData();
      } else {
        setTimeout(() => {
          this.getInfo();
        }, 200);
      }
    },
    editRow(){
      this.$router.push({
        path: "/trainingProgram/trainingObjectiveCreate",
        query: {
          type: 'edit',
          id: this.tmpId,
          pageTitle: this.pageTitleArr
        }
      });
    },
    initData1(id) {
      this.loading = true
      const url = trainingProgramApi.goalInfo({
        id
      });
      this.$get(url)
        .then(res => {
          if (res.status === 1) {
            this.data = res.data;
          } else {
            this.$notify.error({
              title: "error",
              duration: 1500,
              message: res.message
            });
          }
          this.loading = false
        })
        .catch(err => {
          console.log(err);
        });
    },
    initData() {
      this.loading = true;
      const url = trainingProgramApi.goalList;
      this.$post(url, this.page)
        .then(res => {
          if (res.status === 1) {
            if (res.data && res.data.length === 1) {
              this.haveInfo = true;
              this.pageName = res.data[0].versionVal;
              this.initData1(res.data[0].id);
              this.tmpId = res.data[0].id
            } else {
              this.haveInfo = false;
            }
            // this.data = res;
            // this.tableData = res.data;
            // this.tableData.forEach(vo => {
            //   vo.content = vo.content.replace(/<\/?.+?\/?>/g, "");
            //   vo.content = vo.content.replace(/&(lt|gt|nbsp|amp|quot);/g, "");
            // });
          } else {
            this.$notify.error({
              title: "error",
              duration: 1500,
              message: res.message
            });
          }
          this.loading = false;
        })
        .catch(err => {
          console.log(err);
          this.loading = false;
        });
    },
    headInfo(item) {
      this.pageTitleArr = item;
      this.pageTitle = item[1];
    },
    create() {
      this.$router.push({
        path: "/trainingProgram/trainingObjectiveCreate",
        query: {
          type: "add",
          pageTitle: this.pageTitleArr
        }
      });
    }
  }
};
</script>
<style lang="scss">
#objectiveInfo{
  #introduce{
    padding-top: 10px; 
    padding-bottom: 20px; 
    line-height: 2;
      p{
         color:#323232 !important;
         font-size:14px !important;
         font-weight:normal !important;
        span{
          color:#323232 !important;
          font-size:14px !important;
          font-weight:normal !important;
        }
      }
    }
}  
</style>
<style lang='scss'>
@import "../../styles/center.scss";
.table-version {
  .td-content {
    .cell {
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
    }
  }
}
.center_content_name {
  background: #fff;
  border-bottom: 1px solid #f2f2f2;
  height: 80px;
  border-radius: 10px 10px 0 0;
  p {
    font-size: 20px;
    color: #2c3e50;
    font-weight: 700;
    line-height: 50px;
    text-indent: 20px;
  }
}
.center_content_info {
  background: #fff;
  padding: 20px;
  border-radius: 0 0 10px 10px;
  .introduce {
    // background: #f8faff;
    color: #333;
    font-size: $fourteen;
    min-height: 100px;
    padding: 0 20px;
  }
}

.list {
  margin-top: 20px;
  overflow: hidden;
  ul {
    overflow: hidden;
    .targetItem {
      box-sizing: border-box;
      padding: 38px 30px;
      float: left;
      width: 49%;
      overflow: hidden;
      margin-bottom: 20px;
      background-color: #fff;
      border-radius: 10px;
      height: 132px;
      &:nth-of-type(even) {
        margin-left: 2%;
      }
      .index {
        width: 44px;
        height: 44px;
        text-align: center;
        line-height: 44px;
        float: left;
        position: relative;
        span {
          position: absolute;
          left: 50%;
          top: 50%;
          transform: translate(-50%, -50%)
        }
      }
      .itemContent {
        margin-left: 65px;
        line-height: 2;
        color: #323232;
        overflow:hidden;
        font-size:14px;
        display:-webkit-box;
        -webkit-box-orient:vertical;
        -webkit-line-clamp:2;//控制行数
        font-weight: 700;
      }
    }
  }
}
</style>



